
















import Vue from 'vue'
import { Component } from 'vue-property-decorator'
import Tags from '@/components/Money/Tags.vue'
import FormItem from '@/components/Money/FormItem.vue'
import Tabs from '@/components/Tabs.vue'
import NumberPad from '@/components/Money/NumberPad.vue'
import { RecordItem } from '@/types/recorditem'
import store from '@/store/index2'
import typeList from '@/constants/typeList'

@Component({ components: { Tags, FormItem, Tabs, NumberPad } })
export default class Money extends Vue {
  flag=true
  record:RecordItem = {
    tags: [],
    notes: '',
    type: 'consume',
    amount: 0
  }

  typeList = typeList

  created () {
    this.record.type = this.$route.params.type || this.record.type
  }

  onUpdateNotes (value:string) {
    this.record.notes = value
  }

  saveRecord () {
    if (!this.record.tags || this.record.tags.length === 0) {
      window.alert('选择至少一个标签可以帮助更好地分类哦')
    } else {
      store.createRecords(this.record);
      (this.$refs.Tags as any).clearTags()
    }
  }
}
