import createId from '@/lib/createId'
import { Tag } from '@/types/tagList'
const localStorageKeyName = 'tagList'

const tagStore = {
  tagList: [] as Tag[],
  fetchTags () {
    this.tagList = JSON.parse(window.localStorage.getItem(localStorageKeyName) || '[]')
    if (!this.tagList || this.tagList.length === 0) {
      tagStore.createTag('服饰')
      tagStore.createTag('食物')
      tagStore.createTag('住宿')
      tagStore.createTag('日用')
    }
    return this.tagList
  },
  findTag (id:string) {
    return this.tagList.filter(item => item.id === id)[0]
  },
  createTag (name:string) {
    const names = this.tagList.map(item => item.name)
    if (names.indexOf(name) >= 0) {
      window.alert('标签名重复，请您重新创建')
      return 'duplicated'
    } else {
      const id = createId().toString()
      this.tagList.push({ id, name: name })
      this.saveTags()
      return 'success'
    }
  },
  removeTag (id:string) {
    let index = -1
    for (let i = 0; i < this.tagList.length; i++) {
      if (this.tagList[i].id === id) {
        index = i
        break
      }
    }
    this.tagList.splice(index, 1)
    this.saveTags()
    return true
  },
  updateTag (id:string, name:string) {
    const tag = this.tagList.filter(item => item.id === id)[0]
    if (tag.name !== name) {
      tag.name = name
      this.saveTags()
      return 'success'
    } else {
      return 'duplicated'
    }
  },
  saveTags () {
    window.localStorage.setItem(localStorageKeyName
      , JSON.stringify(this.tagList))
  }

}

tagStore.fetchTags()
export default tagStore
